.bio {
  padding: 20px;
  width: 100%;
  display: flex;
  align-items: center;
}

.avatar {
  max-width: 150px;
  height: 150px;
  margin-right: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 100%;
}

.bio_content {
  flex: 1 1 auto;
}

.status {
  margin-bottom: 20px;
  font-weight: 700;
}

.name {
  color: var(--main-bg-color);
  font-weight: 700;
  font-size: 38px;
  margin-bottom: 0;
}

.bio p {
  margin: 5px 0;
}
